import "./termCond-style.css";
const InfoCard = ({ array, tittle }) => {
  return (
    <section id="tc-container">
      <div id="tc-tittle">
        <h1>{tittle}</h1>
      </div>
      <div id="info-div">
        {array.map((items) => {
          return (
            <section className="tc-div">
              <h3 id="infocard-tittle">
                <u>{items.tittle}</u>
              </h3>
              <p className="card-para">{items.description}</p>
              <hr id="hr-tag" />
            </section>
          );
        })}
      </div>
    </section>
  );
};

export default InfoCard;
